div .container {
  text-align: center;
  width: 100%;
  height: 100%;
  display: grid;
  /* grid-template-rows: 10% 90%; */
  background-color: rgb(253, 248, 170);
}

.main {
  display: grid;
  grid-template-columns: 20% 80%;
}

.clientCards {
  display: grid;
  grid-template-rows: 10% 90%;
}

